@import '../../sass/utils/mixin';
@import '../../sass/utils/variables';

$black: #242424;
$gray: #6c6c6c;

.form {
  padding: 64px 38px 198px;
  background: $blue;
  border-radius: 40px 40px 0px 0px;

  @include tablet {
    padding: 34px 50px 32px;
    position: absolute;
    top: 50%;
    left: 50%;

    width: 400px;
    // height: 454px;
    // width: 100%;
    transform: scale(1) translate(-50%, -50%);

    background-color: $dark-blue;
    box-shadow: 0px 24px 38px rgba(0, 0, 0, 0.14),
      0px 9px 46px rgba(0, 0, 0, 0.12), 0px 11px 15px rgba(0, 0, 0, 0.2);
    border-radius: 18px;
  }
}

.title {
  text-align: center;
  margin-bottom: 44px;

  @include font(600, 24px, 1);
  color: $white;

  @include tablet {
    @include font(500, 20px, 1);
  }
}

.inputLabel {
  display: flex;
  align-items: center;

  margin-bottom: 6px;

  @include font(400, 14px, 1.3);

  color: rgba(243, 243, 243, 0.8);

  @include tablet {
    color: $gray;
  }
}

.inputField {
 
  padding: 11px 24px;
  width: 299px;
  height: 40px;

  border: 1px solid rgba(243, 243, 243, 0.4);
  border-radius: 28px;
  &:not(:last-of-type){
    margin-bottom: 18px;
  }

  @include font(400, 14px, 1.3);
  background-color: inherit;
  color: rgba(243, 243, 243, 0.8);

  @include tablet {
    color: $white;
    border: 1px solid rgba(108, 108, 108, 0.4);
  }

  &:focus-visible {
    outline: 1px solid $white;
  }
}

.button {
  margin-top: 52px;
  padding: 12px 36px;
  width: 299px;
  height: 44px;

  @include font(700, 14px, 1.43);

  background-color: $white;
  color: $blue;
  border-radius: 60px;
  border: 1px solid rgba(243, 243, 243, 0.4);

  @include tablet {
    margin-top: 40px;
    background-color: $blue;
    color: $white;
    border: 1px solid $blue;

    &:hover {
      color: $black;
      background-color: $white;
    }
  }

  &:hover {
    color: $black;
  }
  // :active {
  //   transform: scale(0.95);
  // }
}
